/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-create-block-gutenberg-draggable-images {
  border: 1px dotted #f00; }

.gutenberg-draggable-images__input-group {
  width: 100%;
  display: flex;
  flex-flow: column; }

.gutenberg-draggable-images__example-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px 0;
  border-bottom: 1px solid #007cba4a;
  min-height: 200px; }
  .gutenberg-draggable-images__example-controls:nth-child(even) {
    background-color: #80808012; }
  .gutenberg-draggable-images__example-controls_add-img {
    cursor: pointer;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    border: 1px solid #007cba;
    background: #007cba;
    color: #fff;
    padding: 10px 0;
    text-transform: capitalize; }
    .gutenberg-draggable-images__example-controls_add-img:hover {
      background: #fff;
      color: #007cba; }
  .gutenberg-draggable-images__example-controls_img {
    width: 50%; }
  .gutenberg-draggable-images__example-controls_inputs {
    width: 50%; }
    .gutenberg-draggable-images__example-controls_inputs_btn-del {
      cursor: pointer;
      color: red;
      display: block;
      text-align: right;
      font-size: 25px;
      transform: translate(-5px, -36px); }
    .gutenberg-draggable-images__example-controls_inputs_inp-size {
      width: 100%; }
    .gutenberg-draggable-images__example-controls_inputs_z-index {
      width: 100%; }

.gutenberg-draggable-images__notices {
  min-height: 400px;
  position: relative; }
  .gutenberg-draggable-images__notices_draggable_img {
    position: absolute;
    top: 0;
    left: 0; }
